import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import QLearningPage from "./interactive-ml/pages/QLearningPage";
import HomePage from "./portfolio/pages/HomePage";
import TetrisPage from "./tetris/pages/TetrisPage";

function DQNPage() {
  return <div>DQN</div>;
}

function App() {
  console.log("PROCESS ENV", process.env);
  return (
    <>
      <div>HELLO WORLD</div>
      <Router>
        <Switch>
          <Route path={process.env.PUBLIC_URL + "/tetris"}>
            <TetrisPage></TetrisPage>
          </Route>
          <Route path={process.env.PUBLIC_URL + "/qlearning"}>
            <QLearningPage></QLearningPage>
          </Route>
          <Route path={process.env.PUBLIC_URL + "/dqn"}>
            <DQNPage></DQNPage>
          </Route>
          <Route path={process.env.PUBLIC_URL + "/"} exact>
            <HomePage></HomePage>
          </Route>
          <Route>
            <HomePage></HomePage>
          </Route>
        </Switch>
      </Router>
    </>
  );
}

export default App;
